body {
    @extend %baseFont;
    font-size: $baseFontSize;
    color: $textBaseColor;
    line-height: 1.5em;
}

h1 {
    @extend %headingFont;
    font-size: $h1FontSize;
  }
  
  h2 {
    @extend %headingFont;
    font-size: $h2FontSize;
  }
  
  h3 {
    @extend %headingFont;
    font-size: $h3FontSize;
  }
  
  h4 {
    font-size: $h4FontSize;
  }
  
  h5 {
    font-size: $h5FontSize;
  }
  
  h6 {
    font-size: $h6FontSize;
  }
  
  h4, h5, h6 {
    font-family: $headingFont;
    margin: 0px 0px 10px 0px;
    line-height: 1.2;
  }
  
  p {
    margin: 0 0 rem(5px);
  }
  
  a {
    @extend %defaultAnimation;
    @include linkColor();
    text-decoration: none; 
  }


  .h1-font-size {
    font-size: $h1FontSize;
  }

  .h2-font-size {
    font-size: $h2FontSize;
  }

  .h3-font-size {
    font-size: $h4FontSize;
  }

  .h4-font-size {
    font-size: $h4FontSize;
  }

  .h5-font-size {
    font-size: $h5FontSize;
  }

  .h6-font-size {
    font-size: $h6FontSize;
  }

  .font-size-small {
    font-size: $fontSizeSmall;
  }

  .text-uppercase {
    text-transform: uppercase;
  }