.clock{display:flex;font-size:8px}.clock__panel{display:flex;align-items:center;box-shadow:inset 1px 1px 5px #000;padding:.1875rem .125rem;border-radius:3px}.clock__decor{margin:0 .3125rem}.clock__decor-oval{height:5px;width:3px;margin:0 .0625rem;background-color:#f29c2b;border-radius:50%;display:inline-block}.clock__decor-oval--light{opacity:0.2}.clock__decor-oval--half{opacity:0.5}.clock__decor-oval--full{opacity:1}

.location{height:100%;display:flex;align-items:center;text-transform:uppercase;font-size:14px}

.progress-bar__bar{width:100%;height:15px;background-color:#32363f;border-radius:15px;box-shadow:inset 1px 1px 5px #000}.progress-bar__progress{height:100%;width:0%;border-radius:15px;box-shadow:inset 1px 1px 5px #000;background-color:#f29c2b}

.header{width:100%;background-color:#32363f;position:relative;padding:.625rem 0 .3125rem}.header__top{display:flex;align-items:center;margin-bottom:.9375rem}.header__stats{position:absolute;display:flex;justify-content:space-between;align-items:center;width:100%;padding:0 .625rem}.header__clock{width:100%;height:100%;display:flex;justify-content:center;align-items:center}.header__section{padding:0 .3125rem}

.forecast-card{display:flex;justify-content:flex-start;align-items:center;width:100%;background-color:#616366;border-radius:5px;margin-bottom:.3125rem;padding:0 .625rem 0 1.25rem}.forecast-card:first-of-type{margin-top:.3125rem}.forecast-card__day{min-width:2.5rem}.forecast-card__temp{margin:.9375rem}.forecast-card__icon-wrapper{flex-grow:1;display:flex;flex-direction:column;justify-content:center;align-items:flex-end}.forecast-card__desscription{margin-top:-5px}

.forecast-list{padding:0 .1875rem}


a{transition:all .3s ease-in-out}body{font-family:"Roboto",sans-serif;font-weight:400}h1,h2,h3{font-family:"Roboto",sans-serif;font-weight:400;line-height:1.2}*,*:after,*:before{box-sizing:border-box}img{display:block;max-width:100%;height:auto}body{font-size:16px;color:#fff;line-height:1.5em}h1{font-size:20px}h2{font-size:18px}h3{font-size:16px}h4{font-size:14px}h5{font-size:12px}h6{font-size:10px}h4,h5,h6{font-family:"Roboto",sans-serif;margin:0px 0px 10px 0px;line-height:1.2}p{margin:0 0 .3125rem}a{color:#fff;text-decoration:none}a:hover{color:#f29c2b}.h1-font-size{font-size:20px}.h2-font-size{font-size:18px}.h3-font-size{font-size:14px}.h4-font-size{font-size:14px}.h5-font-size{font-size:12px}.h6-font-size{font-size:10px}.font-size-small{font-size:8px}.text-uppercase{text-transform:uppercase}body{background-color:#000}.app{background-color:#101012;max-width:280px;margin:3.125rem auto;width:100%;height:500px}

